<template>
  <div>
    <v-row>
      <v-col class="mt-4" cols="12" md="12">
        <v-card>
          <div class="ml-4 pt-2 header-text-style font-weight-bold">위치 정보</div>
          <div style="border: 1px solid rgb(190, 189, 189); width: 100%; height: 45vh" id="map" />
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  props: ['bandinfo'],
  mounted() {
    this.loadGoogleMapsScript()
    this.$session.$on('gps', this.handleGPSData)
  },
  methods: {
    handleGPSData(data) {
      if (this.$session.user == null) return

      // 수신된 GPS 데이터의 bid와 props로 전달받은 bandId를 비교
      if (data.bid === this.bandinfo.bid) {
        this.gpsData = data
        this.updateMap()
      }
    },

    updateMap() {
      if (!this.map || !this.gpsData) return

      const position = new window.google.maps.LatLng(this.gpsData.latitude, this.gpsData.longitude)

      this.setMarker(position, this.bandinfo.name)

      this.map.panTo(position)
    },
    loadGoogleMapsScript() {
      if (window.google && window.google.maps) {
        this.initMap()
      } else {
        const script = document.createElement('script')
        script.src = `https://maps.googleapis.com/maps/api/js?key=${this.apiKey}&callback=initMap`
        script.async = true
        script.defer = true
        window.initMap = this.initMap
        document.head.appendChild(script)
      }
    },
    initMap() {
      this.map = new window.google.maps.Map(document.getElementById('map'), {
        center: this.mapCenter,
        zoom: 17,
      })
      this.setMarker(this.mapCenter, this.bandinfo.name)
    },
    setMarker(position, label) {
      // 이전 마커 삭제
      if (this.marker) {
        this.marker.setMap(null)
      }

      // 새 마커 생성
      this.marker = new window.google.maps.Marker({
        position: position,
        map: this.map,
        label: {
          text: label,
          color: '#FFF',
        },
      })
    },
  },
  data() {
    return {
      map: null,
      marker: null,
      mapCenter: { lat: 0, lng: 0 },
    }
  },
}
</script>
